<template>
  <div>
      <Head></Head>
      <Answer :Allanswer="Allanswer" :Allquestion="Allquestion" :Texterror="Texterror"></Answer>
  </div>
</template>

<script>
import Answer from "@/components/TextAnswer.vue";
import Head from "@/components/TextHead.vue";
import {marked} from "marked";
import {Configuration, OpenAIApi} from "openai";
export default {
    name: "MyMessage",
    components:{Answer,Head},
    data() {
        return {
            Texterror:JSON.parse(localStorage.getItem('Texterror')) || [],
            Allquestion:JSON.parse(localStorage.getItem('Allquestion')) || [],
            Allanswer:JSON.parse(localStorage.getItem('Allanswer')) || [],
        }
    },
    watch:{
        Texterror:{
            deep:true,
            handler(value){
                localStorage.setItem('Texterror',JSON.stringify(value));
            }
        },
        Allquestion:{
            deep:true,
            handler(value){
                localStorage.setItem('Allquestion',JSON.stringify(value));
            }
        },
        Allanswer:{
            deep:true,
            handler(value){
                localStorage.setItem('Allanswer',JSON.stringify(value));
            }
        },
    },
    methods: {
        async getAnswer(question) {

            this.Allquestion.unshift({id:Date.now(),value:question});
            if(this.$store.state.YON){
                this.Allanswer.unshift('请续费！')
                return
            }
            this.Allanswer.unshift('加载中。。。')
            const configuration = new Configuration({
                apiKey:'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',
            });
            const openai = new OpenAIApi(configuration);
            await openai.createCompletion({
                model: 'text-davinci-003',
                prompt: question,
                temperature:0.7,
                max_tokens:1024,
                top_p:1.0,
                frequency_penalty:0.0,
                presence_penalty:0.0
            }).then(
                response => {
                    let answer=response.data.choices[0].text;
                    let change=marked(answer);
                    // this.Allanswer.push(change);
                    this.Allanswer.splice(0,1,change)
                    this.Texterror.unshift('');
                },
                error => {
                    this.Texterror.unshift(error.message);
                    // this.Allanswer.push(error.message);
                    this.Allanswer.splice(0,1,error.message)
                }
            );
            this.$store.commit('ADD')
            this.$store.dispatch('judge')
            this.$bus.$emit('Judge')
        },
        Delete(value) {
            for (let i=0;i<this.Allquestion.length;i++){
                if(this.Allquestion[i]==value){
                    this.Allquestion.splice(i,1);
                    this.Allanswer.splice(i,1);
                    this.Texterror.splice(i,1);
                }
            }
        }
    },
    mounted() {
        this.$bus.$on('openai',this.getAnswer);
        this.$bus.$on('delete',this.Delete);
    },
    beforeDestroy() {
        this.$bus.$off('openai');
        this.$bus.$off('delete');
    }
}
</script>

