<template>
  <div id="app">
      <Header></Header>
      <router-view></router-view>
  </div>
</template>

<script>
import Header from "@/views/Header.vue";
export default {
    name: 'App',
    components:{Header},
    methods:{
        open() {
            this.$alert('免费次数已用完，请续费使用', '提示!', {
                confirmButtonText: '确定',
                callback: action => {
                    this.$message({
                        type: 'info',
                        message: `action: ${ action }`
                    });
                }
            });
        },
      Judge(){
          if(this.$store.state.YON){
              this.open()
          }
      }
    },
    mounted() {
        this.$bus.$on("Judge",this.Judge)
    }
}
</script>
