<template>
  <div>
      <div class="question">
          <input type="text"
                 v-model="question"
                 placeholder="Please enter the question"
                 @keyup.enter="getImg">
      </div>
  </div>
</template>

<script>
export default {
    name: "ImgHeader",
    data(){
        return{
            question:'',
        }
    },
    methods:{
        getImg() {
            if(!this.question.trim())return;
            this.$emit('getImg',this.question);
            this.question=''
        }
    }
}
</script>

<style scoped>
.question{
    width: 600px;
    position: fixed;
    top: 80px;
    left: 50%;
    margin-left: -300px;
    z-index: 1;
}
.question input{
    width: 585px;
    height: 50px;
    border: none;
    outline:none;
    padding-left: 15px;
    border-radius: 10px;
    font-size: 20px;
    color: #757575;
    box-shadow: 0px 0px 10px #757575;
}
</style>