import VueRouter from "vue-router";
import Img from "@/views/Img.vue";
import Text from "@/views/Text.vue";
export default new VueRouter({
    routes:[
        {
            path:'/Text',
            component:Text,
        },
        {
            path:'/img',
            component:Img
        },
        {
            path:'/',
            redirect:'/Text'
        }
    ]
})