<template>
  <div class="father">
      <div v-html="i.value" class="questionimg"></div>
      <h2 v-show="ALLimage[index]==='加载中。。。'">加载中。。。</h2>
      <div class="img">
          <h2 v-show="error[index]">{{error[index]}}</h2>
          <img v-show="ALLimage[index]&&!error[index]&&ALLimage[index]!='加载中。。。'" :src="ALLimage[index]" alt="生成的图片">
      </div>
      <button class="delete" @click="Delete(i)">删除</button>
  </div>
</template>

<script>
export default {
    name: "ImgItem",
    props:['i','index','ALLimage','error'],
    methods:{
        Delete(i){
            this.$bus.$emit('DeleteImg',i)
        }
    }
}
</script>

<style scoped>
.questionimg{
    padding-left:5px ;
    padding-bottom: 10px;
    background-color: #efefef;
    font-size: 20px;
    color: #757575;
}
h2{
    font-weight: normal;
    font-size: 20px;
    padding-top: 20px;
    color: #757575;
}
.img{
    width: 600px;
    margin-bottom: 30px;
}
img{
    width: 300px;
    margin: 0 auto;
}
.delete{
    display: none;
    width: 50px;
    background-color: #efefef;
    color: #757575;
    height: 30px;
    position: absolute;
    border: none;
    bottom: 1px;
    right: 0px;
}
.father{
    position: relative;
}
.father:hover .delete{
    display: block;
}
</style>