<template>
    <div class="answer father">
        <div class="answer one" v-html="a.value">
        </div>
        <h2 v-show="Allanswer[index]==='加载中。。。'">加载中。。。</h2>
        <h2 v-show="Texterror[index]" v-html="Texterror[index]"></h2>
        <div v-show="Allanswer[index]&&!Texterror[index]&&Allanswer[index]!='加载中。。。'" class="answer two" v-html="Allanswer[index]"></div>
        <button class="delete" @click="Delete(a)">删除</button>
    </div>
</template>

<script>
export default {
    name: "TextItem",
    props:['a','index','Allanswer','Texterror'],
    methods:{
        Delete(value){
            this.$bus.$emit('delete',value);
        }
    }
}
</script>

<style scoped>
.answer{
    width:600px;
    margin: 0 auto;
    font-size: 20px;
    color: #757575;
}
.answer:hover .delete{
    display: block;
}
.one{
    padding-left: 5px;
    padding-bottom: 10px;
    background-color: #efefef;
}
.two{
    padding-bottom: 35px;
}

.father{
    position: relative;
}
h2{
    font-weight: normal;
    font-size: 20px;
    color: #757575;
    margin-bottom: 20px;
}
.delete{
    display: none;
    width: 50px;
    background-color: #efefef;
    color: #757575;
    height: 30px;
    position: absolute;
    border: none;
    bottom: 1px;
    right: 0px;
}
</style>