<template>
    <div class="question">
        <input type="text"
               v-model="question"
               placeholder="请输入问题"
               @keyup.enter="getAnswer">
    </div>
</template>

<script>
export default {
    name: "MyHead",
    data(){
        return{
            question:''
        }
    },
    methods:{
        getAnswer() {
            if(!this.question)return;
            this.$bus.$emit('openai',this.question);
            this.question='';
        }
    }
}
</script>

<style scoped>
.question{
    width: 600px;
    position: fixed;
    top: 80px;
    left: 50%;
    margin-left: -300px;
    z-index: 1;
}
.question input{
    width: 585px;
    height: 50px;
    border: none;
    outline:none;
    padding-left: 15px;
    border-radius: 10px;
    font-size: 20px;
    color: #757575;
    box-shadow: 0px 0px 10px #757575;
}
</style>