<template>
    <div class="openaiImg">
        <ImgHeader @getImg="generateImage"></ImgHeader>
        <ImgAnswer
            :error="error"
            :Allquestionimg="Allquestionimg"
            :ALLimage="ALLimage"
        ></ImgAnswer>
    </div>
</template>

<script>
import {Configuration,OpenAIApi} from "openai"
import ImgHeader from "@/components/ImgHeader.vue";
import ImgAnswer from "@/components/ImgAnswer.vue";
export default {
    name: "MyImg",
    components:{ImgHeader,ImgAnswer},
    data(){
        return{
            error:JSON.parse(localStorage.getItem('error')) || [],
            Allquestionimg:JSON.parse(localStorage.getItem('Allquestionimg')) || [],
            ALLimage: JSON.parse(localStorage.getItem('ALLimage')) || [],
        }
    },
    watch:{
        Allquestionimg:{
            deep:true,
            handler(value){
                localStorage.setItem('Allquestionimg',JSON.stringify(value));
            }
        },
        ALLimage:{
            deep:true,
            handler(value){
                localStorage.setItem('ALLimage',JSON.stringify(value));
            }
        },
        error:{
            deep:true,
            handler(value){
                localStorage.setItem('error',JSON.stringify(value));
            }
        }
    },
    methods:{
        Delete(value) {
            for (let i=0;i<this.Allquestionimg.length;i++){
                if(this.Allquestionimg[i]==value){
                    this.Allquestionimg.splice(i,1);
                    this.ALLimage.splice(i,1);
                    this.error.splice(i,1);
                }
            }
        },
        async generateImage(question) {
            this.Allquestionimg.unshift({id:Date.now(),value:question});
            this.ALLimage.unshift('加载中。。。')
            const configuration = new Configuration({
                apiKey:'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',
            });
            const openai = new OpenAIApi(configuration);
            await openai.createImage({
                prompt: question,
                size: "1024x1024",
                response_format:'url',
                n:1
            }).then(
                response => {
                    this.ALLimage.splice(0,1,response.data.data[0].url)
                    // this.ALLimage.unshift(response.data.data[0].url);
                    this.error.unshift('');
                },
                error => {
                    this.ALLimage.splice(0,1,error.message)
                    // this.error.unshift(error.message);
                    this.ALLimage.unshift(this.error)
                }
            );
            this.$store.commit('ADD')
            this.$bus.$emit('Judge')
        },
    },
    mounted() {
        this.$bus.$on('DeleteImg',this.Delete)
    },
    beforeDestroy() {
        this.$bus.$off('DeleteImg')
    }
}
</script>

